  private api = "https://ynsat.herokuapp.com";
  constructor(private http: HttpClient) {
  }
  //Pruebas de la API REST ejercicios 
  getEjerciciosAPI() 
  {
    const path = `${this.api}/ejercicios`;
    return this.http.get<Ejercicio[]>(path);
  }

  getEjercicioAPI(id: string)
  {
    const path = `${this.api}/ejercicios${id}`;
    return this.http.get<Ejercicio>(path);
  }
  addEjerciciosAPI(ejercicio: Ejercicio){
    const path = `${this.api}/ejercicios`;
    return this.http.put<Ejercicio>(path,ejercicio)
  }
  //No funciona
  getEjercicioMAP(id: string)
  {
    const path = `${this.api}/ejercicios${id}`;
    return this.http.get<Ejercicio["lat"]>(path);
  }
 
  //Estos son los que estamos usando y funcionan bien
  getEjercicios(type) 
  {
    //Look inside the JSON to find the theme according of the type received
    var results = [];
    var searchField = "theme";
    var searchVal = type;
    for (var i=0 ; i < this.ejercicios.length ; i++)
{
    if (this.ejercicios[i][searchField] == searchVal) {
        results.push(this.ejercicios[i]);
    }
}
    return results
  }

  getEjercicio(ejercicioId: string)
  {
    return{
      ...this.ejercicios.find(ejercicio =>{
        return ejercicio.id ===ejercicioId
      })
    }
  }

