Blockly.Extensions.register('student_dynamic_dual',
  function() {
    this.getInput('INPUT')
      .appendField(new Blockly.FieldDropdown(
        function() {
          var options = [];
          var coincidence = [];
          var searchVal = "espaa";
          var block = this.getSourceBlock();
          for (var i=0 ; i < list.length ; i++)
        {
          options.push([list[i][0],list[i][1]]);
        }
          if (block){
            searchVal = block.getFieldValue('input1');
          }
          for (var j=0 ; j < list.length ; j++)
        {
          if (list[j][0].toLowerCase().includes(searchVal.toLowerCase())){
            coincidence.push([list[j][0],list[j][1]]);
          }
          if (coincidence.length > 0){
             options = coincidence;
          }
        }
          return options;
        }
      ), 'country'
    );
  }
);

